.combine-card{
  width: 37.8125em;
  position: relative;
  &__img{
    img{
      width: 100%;
      display: block;
    }
  }
  &__text{
    padding: 1.8em 0 0 4.5em;
    max-width: 26em;
  }
  &__nav{
    display: flex;
  }
  &__btn{
    display: flex;
    color: $primary;
    &:hover{
      color: $black;
      .combine-card__btn-icon{
        background-color: $black;
      }
      .combine-card__btn-text{
        border-color: $black;
      }
    }
    &-text{
      text-transform: uppercase;
      border-bottom: 1px dashed $primary;
    }
    &-icon{
      transition: inherit;
      @include center;
      width: 16.9375em;
      height: 16.9375em;
      background-color: $primary;
      border-radius: 50%;
      img{
        width: 10.625em;
      }
    }
  }
}

.info-card{
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  &__icon{
    height: 2.5em;
    margin-bottom: 1.37em;
    img{
      height: 100%;
      display: block;
    }
  }
  &__title{
    text-transform: uppercase;
    color: $primary;
  }
  &__hint,
  &__item{
    margin-top: .8em;
  }
}

.work-card{
  &__img{
    img{
      display: block;
      width: 100%;
    }
  }
}

.review-card{
  height: 100%;
  padding: 1.88em 2.44em 2.88em;
  background-color: #fff;
  &__top{
    @include grid100gap(.56em);
  }
  &__quote{
    display: block;
    margin-top: 1.38em;
    width: 2.5em;
  }
  &__text{
    margin-top: 1.25em;
  }
}

.step-card{
  height: 15em;
  padding-left: 1.3em;
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  &:last-child{
    .step-card__num:before,
    .step-card__num-top{
      background: #151415;
    }
    .step-card__num-bottom{
      border-color: #151415;
    }
  }
  &:before{
    content: '';
    position: absolute;
    top: 5.5em;
    left: 1.3em;
    width: 100%;
    height: 1px;
    border-top: 1px dashed #000;
  }
  &__num{
    margin-left: -1.3em;
    display: flex;
    flex-direction: column;
    align-items: center;
    position: relative;
    z-index: 2;
    &:before{
      content: '';
      display: block;
      height: 2em;
      width: .1875em;
      background-color: $primary;
    }
    &-top{
      order: -1;
      width: 3.125em;
      height: 3.125em;
      background-color: $primary;
      border-radius: 50%;
      @include center;
      font-weight: 900;
      color: #fff;
      span{
        font-size: 1.5em;
        line-height: 123%;
      }
    }
    &-bottom{
      width: .6875em;
      height: .6875em;
      border-radius: 50%;
      border: .1875em solid $primary;
      background-color: #fff;
    }
  }
  &__bottom{
    @include grid100gap(.3125em);
    margin-top: .625em;
  }
}


@media screen and (max-width: 992px){
  .combine-card{
    width: unset;
    &__text{
      padding: 1em 0;
    }
    &__btn{
      &-icon{
        display: none;
      }
    }
  }

  .review-card{
    padding: 1em 1.25em;
  }
}


@media screen and (max-width: 576px){
  .step-card{
    height: unset;
  }
}
