.footer{
  padding: 1.5em 0;
  background: #151415;
  color: #fff;
  &__inner{
    display: flex;
    align-items: center;
  }
  .contacts{
    margin-left: auto;
  }
  &__info{
    @include grid100gap(.2em);
    margin-left: 5.28em;
    button{
      color: $gray;
      &:hover{
        color: $primary;
      }
    }
  }
  &__by{
    display: inline-flex;
    align-items: center;
    margin-top: auto;
    color: #fff;
    margin-left: 3em;
    svg{
      width: 2.5em;
      height: auto;
      margin-right: 1em;
    }
    span{
      font-size: .75em;
      line-height: 150%;
    }
  }
}


@media screen and (max-width: 992px){
  .footer{
    padding: 1em 0;
    &__inner{
      padding-bottom: 5em;
      position: relative;
    }
    &__info{
      margin-left: 2em;
    }
    &__by{
      margin: 0;
      position: absolute;
      bottom: 0;
      left: 50%;
      transform: translateX(-50%);
      svg{
        width: 2em;
      }
      span{
        font-size: .75em;
      }
    }
  }
}


@media screen and (max-width: 576px){
  .footer{
    &__inner{
      flex-wrap: wrap;
    }
    &__info{
      margin-left: auto;
    }
    .contacts{
      width: 100%;
      margin: 1em 0 0;
      justify-content: flex-end;
    }
    .social{
      margin-right: auto;
      margin-left: 2em;
    }
    &__by{
      left: 0;
      transform: unset;
    }
  }
}
