@import "../vars";
.m-btn {
    display: flex;
    align-items: center;
    justify-content: center;
    transition: all ease-out .225s;
    padding: 0;
    text-transform: uppercase;
    font-weight: 500;
    &:focus {
        box-shadow: none;
    }
    &__text{
        font-size: 1.5em;
    }
    &__icon{
        position: absolute;
        right: 0;
        top: 0;
        width: 6.8em;
        height: 100%;
        @include center;
        img,
        svg{
            height: 3em;
            width: auto;
        }
    }
}

.m-btn-big{
    height: 7.5em;
    padding: 0 6.8em 0 0;
    position: relative;
    &:before{
        content: '';
        position: absolute;
        bottom: 0;
        left: 0;
        width: 100%;
        height: .625em;
        z-index: 2;
        transition: inherit;
    }
}

.m-btn-white{
    background-color: #fff;
    color: $primary;
    &:hover{
        color: #fff;
        background-color: $primary;
        .m-btn__icon{
            border-color: #fff;
        }
        path{
            fill: #fff;
        }
        &:before{
            background-color: $brown;
        }
    }
    &:before{
        background-color: $primary;
    }
    .m-btn__icon{
        border-left: 1px solid $primary;
    }
}

.m-btn-primary{
    background-color: $primary;
    color: #fff;
    &:hover{
        color: #fff;
        background-color: darken($primary,10%);
        .m-btn__icon{
            border-color: #fff;
        }
        path{
            fill: #fff;
        }
    }
    &:before{
        background-color: $brown;
    }
    .m-btn__icon{
        border-left: 1px solid #fff;
    }
}

.play-btn{
    width: 8.9375em;
    height: 8.9375em;
    @include centerAbsolute;
    @include center;
    border-radius: 50%;
    background-color: rgba(0, 0, 0, 0.40);
    &:hover{
        background-color: rgba(0, 0, 0, 0.70);
    }
    img{
        width: 3.9375em;
    }
}


@media screen and (max-width: 992px){
    .m-btn{
        &__icon{
            width: 4.5em;
            img,
            svg{
                height: 2em;
            }
        }
        &__text{
            font-size: 1.25em;
        }
    }

    .m-btn-big{
        height: 5em;
        padding-right: 4.5em;
        &:before{
            height: .5em;
        }
    }
}
