.main{
  padding: 20.25em 0 9.8em;
  position: relative;
  background: #000;
  color: #fff;
  .container{
    position: relative;
    z-index: 2;
    @media screen and (min-width: 993px){
      padding: 0 22em;
    }
  }
  &__top{
    display: flex;
    align-items: flex-end;
  }
  &__img{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
    display: block;
  }
  &__title{
    padding: 0 .5em 2.75em 0;
  }
  &__logo{
    font-weight: 700;
    text-transform: uppercase;
    position: relative;
    &:before{
      content: '';
      position: absolute;
      left: 0;
      bottom: 1em;
      width: 100%;
      height: 1px;
      background-color: #fff;
    }
    span{
      display: block;
      font-size: 12.5em;
      line-height: 123%;
    }
  }
  &__desc{
    margin-top: 3.5em;
    max-width: 42.7em;
  }
  &__nav{
    display: flex;
    margin-top: 6.4em;
  }
  &__btn{
    width: 30em;
    margin-right: 1.5em;
  }
}

.variant{
  padding: 5.625em 0 .8em;
  &__top,
  &__bottom{
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  .info-block{
    max-width: 40em;
  }
  &__arrow{
    margin: 0 1.5em;
    width: 12.7em;
  }
  .gallery{
    width: 50.4375em;
    margin-right: -13.5em;
  }
}

.variant-info{
  width: 33.875em;
  margin: 0 8em;
  flex-shrink: 0;
  &__desc{
    max-width: 27.5em;
  }
  &__items{
    margin-top: 3.7em;
    @include grid100gap(2.4em);
  }
  &__item{
    display: flex;
    &-text{
      padding-top: 2.875em;
    }
  }
  &__icon{
    width: 5em;
    height: 5em;
    border-radius: 50%;
    flex-shrink: 0;
    margin-right: 7.19em;
    background-color: $primary;
    @include center;
    position: relative;
    &:before{
      content: '';
      position: absolute;
      width: 8.75em;
      height: 1px;
      background-color: $primary;
      left: 2.5em;
      top: 4.0625em;
    }
    img{
      width: 2.625em;
    }
  }
}

.gallery{
  position: relative;
  &__img{
    img{
      width: 100%;
      height: 100%;
      object-fit: cover;
      display: block;
    }
  }
  &__small{
    position: absolute;
    bottom: 1.37em;
    right: .875em;
    width: 21.875em;
    .swiper{
      &-slide{
        width: 20%;
        padding-right: .625em;
        cursor: pointer;
        &-thumb-active,
        &:hover{
          .gallery__img:before{
            opacity: 0;
          }
        }
        &-thumb-active{
          .gallery__img{
            border-color: #fff;
          }
        }
      }
    }
    .gallery__img{
      border: 1px solid transparent;
      transition: all .4s;
      @include imgCover(calc(100% - .3125em));
      &:before{
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background-color: #fff;
        opacity: .75;
        z-index: 2;
        transition: inherit;
      }
    }
  }
  &__nav{
    position: absolute;
    left: 0;
    bottom: 0;
    display: flex;
  }
  .swiper{
    &-wrapper{
      align-items: stretch;
    }
    &-slide{
      height: auto;
    }
    &-button{
      width: 6.25em;
      height: 6.25em;
      @include center;
      background-color: #fff;
      position: static;
      transition: all .3s;
      &:hover{
        background-color: $primary;
      }
      &-next{
        border-left: 1px solid #C5C5C5;
      }
      img{
        width: 1.25em;
      }
    }
  }
}

.map{
  height: 30.3125em;
}

.combine{
  padding: .8em 0 4.94em;
  .info-block{
    max-width: 51em;
    &:before{
      top: .4em;
    }
  }
  &__items{
    display: flex;
    justify-content: space-between;
    margin-top: 3em;
  }
  .combine-card{
    @media screen and (min-width: 993px){
      .combine-card__img,
      .combine-card__text{
        position: relative;
        z-index: 2;
      }
      &:nth-child(1){
        .combine-card__btn{
          position: absolute;
          top: 5.44em;
          left: 100%;
          margin-left: -4.625em;
          flex-direction: column-reverse;
          align-items: flex-end;
          &-icon{
            margin-bottom: 1.8em;
          }
          &-text{
            transform: translateX(2.33em);
          }
        }
      }
      &:nth-child(2){
        width: 30em;
        margin-top: -24em;
        .combine-card{
          &__nav{
            justify-content: flex-end;
            margin-top: 1.7em;
            padding-right: 2em;
          }
          &__btn{
            &-icon{
              position: absolute;
              right: -13.5em;
              top: 100%;
              margin-top: -7.5625em;
            }
          }
        }
      }
    }
  }
}

.info{
  padding: 5em 0 6.25em;
  background: #151415;
  color: #fff;
  position: relative;
  .info-block{
    &:before{
      top: .75em;
    }
  }
  &__grid{
    display: grid;
    grid-template-columns: repeat(4,calc(25% - 3em / 4));
    gap: 1em;
    margin-top: 3.8em;
  }
  &__btn{
    position: absolute;
    bottom: 0;
    left: 0;
    height: 6.25em;
    width: 100%;
    font-weight: 500;
    text-transform: uppercase;
    span{
      font-size: 1.5em;
      line-height: 130%;
    }
    img{
      width: 10.25em;
      margin-left: 2.44em;
      flex-shrink: 0;
    }
  }
}

.info-contacts{
  margin-top: 3.875em;
  padding: 3.69em .75em;
  border-top: 1px solid #3A383A;
  @include center;
  flex-direction: column;
}

.works{
  padding: 5.625em 0 4.75em;
  overflow: hidden;
  &__grid{
    display: grid;
    grid-template-columns: 33.5em calc(100% - 38.3em);
    gap: 4.8em;
  }
  &__desc{
    margin-top: 3.5em;
  }
}

.work-slider,
.review-slider{
  width: calc(100% + 13.5em);
  overflow: hidden;
  .swiper{
    &-container{
      max-width: calc(100% - 2em);
    }
    &-wrapper{
      align-items: stretch;
    }
    &-slide{
      width: 20.875em;
      height: auto;
      padding-right: 1.44em;
    }
  }
}

.review{
  padding: 5.625em 0 3.38em;
  background: #F4F0EF;
  &__grid{
    display: grid;
    grid-template-columns: 26em calc(100% - 32em);
    gap: 6em;
  }
}

.review-slider{
  .swiper{
    &-container{
      max-width: calc(100% - 12.25em);
      overflow: hidden;
    }
    &-slide{
      width: 50%;
      padding-right: 1.25em;
    }
  }
}

.consult{
  padding-top: 1.5625em;
  background-size: cover;
  background-position: center;
  margin-bottom: 4em;
}

.consult-form{
  max-width: 100%;
  width: 36.125em;
  background-color: #fff;
  transform: translateY(4em);
  padding: 2.6em;
  box-shadow: 0 .25em .25em 0 rgba(0, 0, 0, 0.25);
  &__desc{
    margin-top: 3.4em;
  }
  &__fields{
    margin-top: 1.6em;
    @include grid100gap(1.6em);
  }
  &__btn{
    margin-top: 2.56em;
    width: 100%;
  }
  &__hint{
    margin-top: 1.7em;
    text-align: center;
  }
}

.debt{
  padding: 3.4em 0 0;
  &__inner{
    display: grid;
    grid-template-columns: calc(100% - 35em) 30em;
    gap: 5em;
  }
  &__col{
    max-width: 55em;
    position: relative;
  }
  &__img{
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    display: block;
  }
  .info-block{
    position: relative;
    z-index: 2;
    &:before{
      top: .4em;
    }
    .text24{
      margin-bottom: .67em;
    }
  }
  &__title{
    display: inline-block;
    padding-right: 14.75em;
    position: relative;
    .text96{
      position: relative;
      z-index: 2;
    }
  }
  @media screen and (min-width: 993px){
    &__sale{
      width: 16.9375em;
      height: 16.9375em;
      @include center;
      position: absolute;
      right: 0;
      top: 50%;
      transform: translateY(-50%);
      background-color: $primary;
      border-radius: 50%;
      color: #fff;
      &-num{
        font-size: 7.5em;
        line-height: 123%;
      }
      &-title{
        position: absolute;
        top: 2.87em;
        left: 3.94em;
        text-transform: uppercase;
        span{
          font-size: 2.25em;
        }
      }
    }
  }

}

.debt-form{
  padding-bottom: 4.2em;
  @include grid100gap(1.6em);
  &__btn{
    margin-top: 1.5em;
    width: 100%;
  }
  &__hint{
    text-align: center;
  }
}

.production{
  padding-top: 4.2em;
  color: #fff;
  background: #151415;
  .info-block{
    max-width: 48em;
    &:before{
      top: 1.85em;
    }
  }
  &__grid{
    margin-top: .56em;
    display: grid;
    grid-template-columns: calc(100% - 56em) 53.4375em;
    gap: 1.5em;
  }
  &__info{
    @include grid100gap(1.19em);
  }
  &__desc{
    margin-top: 6.87em;
    max-width: 27em;
  }
}

.steps{
  padding: 3em 0;
  &__inner{
    display: grid;
    grid-template-columns: 22em calc(100% - 25em);
    gap: 1.5em 3em;
  }
  .info-block{
    &:before{
      top: 2.5em;
    }
  }
  &__top{
    display: flex;
    align-items: flex-end;
    .text48{
      padding-bottom: .25em;
    }
  }
  &__day{
    span{
      font-size: 11.25em;
    }
  }
  &__grid{
    display: grid;
    grid-template-columns: repeat(3,calc(100% / 3));
    grid-auto-flow: dense;
  }
  @media screen and (min-width: 577px){
    &__grid{
      position: relative;
      background-color: #fff;
      &:before{
        content: '';
        position: absolute;
        z-index: -1;
        right: -4.3em;
        top: 5.5em;
        height: 15.125em;
        width: 25em;
        border: 1px dashed #000;
        border-radius: 3.125em;
        border-left: transparent;
      }
    }
    .step-card{
      &:nth-child(5){
        grid-column: 2/3;
        grid-row: 2/3;
      }
      &:nth-child(6){
        grid-column: 1/2;
        grid-row: 2/3;
      }
    }
  }
}



@media screen and (max-width: 992px){
  .main{
    padding: 8em 0 2em;
    &__top{
      flex-direction: column;
      align-items: flex-start;
    }
    &__title{
      padding-bottom: .25em;
    }
    &__logo{
      span{
        font-size: 4.5em;
      }
      &:before{
        bottom: 0;
      }
    }
    &__desc{
      margin-top: 1.5em;
    }
    &__nav{
      margin-top: 2.5em;
    }
  }

  .variant{
    padding-top: 2em;
    &__arrow{
      display: none;
    }
    &__bottom{
      display: block;
    }
    .gallery{
      margin: 1em 0 0 -.75em;
      width: calc(100% + 1.5em);
      padding-left: 0;
      padding-right: 0;
    }
  }

  .variant-info{
    margin: 1em 0 0;
    width: 100%;
    &__desc{
      max-width: 100%;
    }
    &__items{
      grid-template-columns: repeat(2,calc(50% - 1em));
      gap: 1em 2em;
      margin-top: 2em;
    }
    &__item{
      flex-direction: column;
      align-items: center;
      text-align: center;
      &-text{
        padding: 1em 0 0;
      }
    }
    &__icon{
      margin: 0;
      font-size: .875em;
      &:before{
        display: none;
      }
    }
  }

  .map{
    height: 20em;
  }

  .combine{
    padding-bottom: 1.5em;
    &__items{
      display: grid;
      grid-template-columns: repeat(2,calc(50% - .75em));
      gap: 1.5em;
      margin-top: 2em;
    }
  }

  .info{
    padding: 2em 0 5em;
    &__grid{
      grid-template-columns: repeat(2,calc(50% - .5em));
      gap: 2em 1em;
      margin-top: 2em;
    }
    &__btn{
      height: 5em;
      span{
        font-size: 1em;
      }
      img{
        width: 4em;
        margin-left: .75em;
      }
    }
  }

  .info-contacts{
    padding: 1.5em 1em;
  }

  .works,
  .review{
    padding: 1.5em 0;
    overflow: hidden;
    &__grid{
      grid-template-columns: 100%;
      gap: 2em;
    }
    &__desc{
      margin-top: 1.5em;
    }
  }

  .review-slider,
  .work-slider{
    width: 100%;
    padding-right: 0;
    overflow: unset;
    .swiper{
      &-container{
        max-width: 100%;
      }
      &-nav,
      &-pagination-progressbar{
        display: none;
      }
      &-slide{
        width: 18em;
      }
    }
  }

  .review{
    &__desc{
      margin: 0;
    }
  }

  .review-slider{
    .swiper{
      &-container{
        overflow: unset;
      }
      &-slide{
        width: 75%;
      }
    }
  }

  .consult{
    padding: 1.5em 0;
    margin: 0;
  }

  .consult-form{
    margin: 0 auto;
    transform: unset;
    padding: 1em 1.5em;
    width: 28em;
    box-shadow: 0 .25em .25em 0 rgba(0,0,0,.25),
                0 -.25em .25em 0 rgba(0,0,0,.25);
    &__desc{
      margin-top: 1.5em;
    }
  }

  .debt{
    padding: 1.5em 0;
    &__inner{
      grid-template-columns: repeat(2,calc(50% - .75em));
      gap: 1.5em;
    }
    &__title{
      padding: 0;
      display: block;
    }
    &__sale{
      color: $primary;
      &-title,
      &-num{
        display: inline-block;
        text-transform: uppercase;
        line-height: 123%;
      }
      &-title{
        font-size: 1.5em;
      }
      &-num{
        font-size: 2em;
      }
    }
    &__img{
      display: none;
    }
  }

  .debt-form{
    padding: 0;
  }

  .production{
    padding: 1.5em 0;
    &__grid{
      grid-template-columns: 100%;
    }
    &__desc{
      margin-top: 1.5em;
    }
  }

  .steps{
    padding: 1.5em 0;
    &__inner{
      grid-template-columns: 100%;
      max-width: calc(100% - 3em);
    }
    &__day{
      span{
        font-size: 8em;
      }
    }
    &__grid{
      &:before{
        height: 15.0625em;
        right: -4.4em;
      }
    }
  }
}


@media screen and (max-width: 576px){
  .main{
    padding-top: 6em;
    &__nav{
      margin-top: 1em;
      @include grid100gap(1em);
    }
    &__btn{
      margin: 0;
      width: 100%;
    }
  }

  .variant-info{
    &__items{
      grid-template-columns: repeat(2,calc(50% - .5em));
      gap: 1em;
    }
    &__item{
      .text18{
        font-size: .875em;
      }
    }
  }

  .gallery{
    &__small,
    &__nav{
      display: none;
    }
    .swiper{
      &-slide{
        width: 90%;
        padding-right: .5em;
      }
    }
  }

  .combine{
    padding-bottom: 1.5em;
    &__items{
      grid-template-columns: 100%;
    }
  }

  .info{
    .info-block{
      text-align: center;
      .text48{
        font-size: 1.25em;
      }
    }
    &__grid{
      grid-template-columns: 100%;
    }
  }

  .review-slider{
    .swiper{
      &-slide{
        width: 90%;
      }
    }
  }

  .consult{
    padding: 0;
    background: unset;
  }

  .debt{
    &__inner{
      grid-template-columns: 100%;
    }
  }

  .steps{
    &__grid{
      grid-template-columns: 100%;
      gap: 1.5em;
    }
  }
}
