.modal {
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    height: calc(100 * var(--vh));
    overflow-y: auto;
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 80;
    opacity: 0;
    pointer-events: none;
    transition: all .3s;
    &__bg {
        background: rgba(26, 26, 26, 0.85);
        position: fixed;
        width: 100%;
        height: calc(100 * var(--vh));
        top: 0;
        left: 0;
        display: none;
        z-index: 2;
    }

    &._active {
        opacity: 1;
        pointer-events: unset;

        .modal__bg {
            display: block;
        }
    }

    &__dialog {
        background: #fff;
        width: 36.125em;
        max-width: calc(100% - 1em);
        position: relative;
        z-index: 3;
    }
    &__scroll{
        overflow-y: auto;
        max-height: calc(100 * var(--vh));
        padding: 2.625em 3em 3em;
    }
    &__close {
        position: absolute;
        border-top: 0;
        top: 2.56em;
        right: 1.56em;
        background-color: transparent;
        width: 3.125em;
        svg {
            width: 100%;
            height: auto;
        }
    }
    .consult-form{
        box-shadow: unset;
        background-color: transparent;
        padding: 0;
        transform: unset;
        .text96,
        .text48{
            line-height: 100%;
        }
        &__desc{
            margin-top: 1.6em;
        }
    }
}

.conf-modal{
    .modal{
        &__bg{

        }
        &__scroll{
            padding: 0;
        }
        @media screen and (min-width: 993px){
            &__dialog {
                background: #fff;
                width: 92.5em;
                max-width: calc(100% - 8em);
            }
            &__scroll{
                max-height: calc(100 * var(--vh) - 7em);
            }
            &__close{
                right: unset;
                left: 100%;
                top: unset;
                bottom: 100%;
            }
        }
    }
    &__top{
        padding: 3.75em 2em 3.125em;
        text-align: center;
        border-bottom: 1px solid #CED6E1;
    }
    &__text{
        padding: 1.875em 5em 4.75em;
        //color: $dark;
        @include grid100gap(1.25em);
        h3{
            font-weight: 700;
            font-size: 1.25em;
            line-height: 125%;
        }
        p{
            font-size: 1.125em;
            line-height: 122%;
        }
        hr{
            display: block;
            height: .625em;
            border: none;
        }
        strong{
            font-weight: 800;
        }
    }
}


@media screen and (max-width: 992px){
    .modal{
        //display: block;
        overflow-y: auto;
        //align-items: unset;
        overflow-x: hidden;
        &__close{
            margin: 0;
            left: unset;
            bottom: unset;
            right: .5em;
            top: .5em;
            path{
                fill: $black;
            }
        }
        &__scroll{
            padding: 2em 1em 6em;
            max-height: unset;
            overflow: unset;
            overflow-x: hidden;
        }
        &__dialog{
            max-height: calc(100 * var(--vh));
            overflow-y: auto;
            overflow-x: hidden;
            padding-bottom: 1em;
        }
    }

    .conf-modal{
        &__top{
            padding: 3em 1em 1em;
        }
        &__text{
            padding: 1em;
            font-size: .75em;
        }
    }
}
