.test {
  background: #F4F0EF;
  &__inner {
    display: flex;
    align-items: stretch;
  }
  &__left {
    width: 34.6875em;
    margin-right: 21.5625em;
    position: relative;
    z-index: 1;
    padding: 3.75em 0;
  }
  &__right {
    width: 50.4375em;
    margin-right: -13.5em;
    position: relative;
  }
  &__title {
    &:before {
      top: 0.75em;
    }
    span {
      display: block;
    }
  }
  &__images {
    display: grid;
    grid-template-columns: repeat(3, calc(100% / 3));
    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
      display: block;
    }
  }
  &__btn {
    position: absolute;
    top: 13.1875em;
    left: -15.625em;
    width: 29.8125em;
    height: 6.875em;
  }
  &__img {
    position: absolute;
    top: 50%;
    left: -26.9375em;
    transform: translate(0, -50%);
    width: 16.9375em;
    height: 16.9375em;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 50%;
    background: transparent;
    border: 0.0625em solid #D79662;
  }
}

.conditions {
  padding: 5.25em 0 6.25em;
  position: relative;
  &__img {
    position: absolute;
    top: 0;
    right: 0;
    width: 45.3125em;
  }
  &__inner {
    max-width: 45.625em;
    position: relative;
  }
  &__img2 {
    position: absolute;
    z-index: 1;
    right: -22.1875em;
    bottom: 0;
    width: 16.9375em;
    height: 16.9375em;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 50%;
    background: transparent;
    border: 0.0625em solid #D79662;
  }
  &__title {
    span {
      display: block;
    }
    .text24 {
      margin-top: 0.75em;
    }
  }
  &__text {
    max-width: 36em;
    margin-top: 2.1875em;
  }
  .variant-info__items {
    padding-left: 8.125em;
  }
}

.text180 {
  font-size: 11.25em;
  line-height: 90%;
}
.text32 {
  font-size: 2em;
  line-height: 160%;
}

.production2 {
  background: #F4F0EF;
  padding: 3.75em 0;
  &__inner {
    display: flex;
  }
  &__desc {
    width: 21.5625em;
    margin-right: 2.5em;
  }
  &__subtitle {
    margin-top: 1.25em;
    span {
      color: #D79662;
    }
  }
}

.production-cards {
  width: calc(100% - 24.0625em);
  display: grid;
  grid-template-columns: repeat(3, calc(100% / 3 - 2.5em / 3));
  gap: 1.25em;
}

.production-card {
  background: #ffffff;
  padding: 3.75em 1em 2em;
  &__img {
    position: relative;
    padding-bottom: 93%;
    img {
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      width: 100%;
      height: 100%;
    }
  }
  &__title {
    padding-left: 1em;
    margin-top: 3.75em;
  }
}

.info2 {
  &__inner {
    display: flex;
    align-items: stretch;
  }
  &__desc {
    width: 52.625em;
    margin-right: 4.3125em;
    padding: 4.6875em 0;
  }
  &__img {
    width: 50.4375em;
    margin-right: -13.5em;
    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
      display: block;
    }
  }
  &__desc-top {
    display: flex;
  }
  &__title {
    width: 20.4375em;
    margin-right: 5.875em;
    &:before {
      top: 0.875em;
    }
    span {
      display: block;
    }
    .text24 {
      margin-top: 0.875em;
    }
  }
  &__text {
    width: calc(100% - 26.3125em);
  }
  &__desc-bottom {
    max-width: 42.625em;
    margin-top: 3.75em;
  }
  &__subtitle {
    padding-bottom: 1.25em;
    position: relative;
    &:after {
      content: '';
      position: absolute;
      right: 0;
      bottom: 0;
      width: 56.25em;
      height: 0.0625em;
      background: #000000;
    }
    .text32 {
      color: #D79662;
    }
    .text24 {
      span {
        color: #D79662;
      }
    }
  }
  &__text2 {
    margin-top: 1.25em;
  }
}



@media screen and (max-width: 992px) {
  .test {
    padding: 2em 0;
    &__inner {
      display: block;
    }
    &__left {
      width: 100%;
      margin: 0 0 2.5em;
      padding: 0;
    }
    &__right {
      width: 100%;
    }
    &__img {
      display: none;
    }
    &__btn {
      position: relative;
      top: 0;
      left: 0;
      height: 5em;
      margin-bottom: 1em;
    }
  }

  .conditions {
    padding: 2em 0;
    &__img {
      display: none;
    }
    &__inner {
      max-width: 100%;
    }
    &__text {
      max-width: 100%;
    }
    .variant-info__items {
      padding-left: 0;
    }
    .variant-info__item-text {
      br {
        display: none;
      }
    }
    &__img2 {
      display: none;
    }
  }

  .text180 {
    font-size: 4.5em;
  }
  .text32 {
    font-size: 1.5em;
  }

  .production2 {
    padding: 2em 0;
    &__inner {
      display: block;
    }
    &__desc {
      width: 100%;
      margin: 0 0 2.5em 0;
    }
    &__title {
      span {
        display: block;
      }
    }
  }

  .production-cards {
    width: 100%;
    grid-template-columns: repeat(2, calc(50% - 1.25em / 2));
  }

  .production-card {
    &__title {
      padding-left: 0;
    }
  }

  .info2 {
    padding: 2em 0;
    &__inner {
      display: block;
    }
    &__desc {
      width: 100%;
      padding: 0;
    }
    &__img {
      width: 100%;
      margin-top: 2.5em;
      margin-right: 0;
    }
    &__desc-top {
      display: block;
    }
    &__title {
      width: 100%;
      margin: 0 0 2em 0;
    }
    &__text {
      width: 100%;
    }
    &__desc-bottom {
      max-width: 100%;
      margin-top: 2em;
    }
  }
}

@media screen and (max-width: 576px) {
  .test {
    &__btn {
      width: 100%;
    }
  }
}
